import axios from 'axios'
import React, { useState, useEffect, useContext } from 'react' 
import api from '../../API/api'
import { useHistory } from 'react-router'
import { TrabajadoresContext } from "../../Context/Context"

const Login =  () =>  {
    const history = useHistory()

    const {userInfo} = useContext(TrabajadoresContext)
    const [email,setEmail] = useState("")
    const [password,setPassword] = useState("")
    const [userName, setUserName] = useState('')

    const handleEmail = (e) => {
        setUserName(e.target.value)
    }

    const handlePassword = (e) => {
        setPassword(e.target.value)    
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        try{
        const response = await api.post("users/SignIn",{
            correo:email,
            contraseña:password
        })
        history.push("/")
        
    }
    catch(err){
        console.log(err)
    }
    }
    
    const handleRedirect = () => {
        history.push("/")
    }


    useEffect(()=>{
        const validateUserInfo = ()=>{

        }
    }, [])

    return (
            <div>
            <form id="login-usuario">
                <div>
                <label htmlFor="email">Email</label>
                 <input class='input_not_submit' type="text" value={userName} placeholder="Escriba su Correo Electrónico" onChange={handleEmail}/>
                </div>

                <div>
                <label htmlFor="contraseña">Contraseña</label>
                    <input class='input_not_submit' type="password" value={password} placeholder="Escriba su contraseña" onChange={handlePassword}/>
                </div>

                <div>
                    <input class="submit" type="submit" onClick={handleSubmit}/>
                </div>
            </form>
            </div>
    )

}

export default Login;